.register {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.2) 0%,
      rgba(0, 0, 0, 1) 100%
    ),
    url("https://ceotudent.com/wp-content/uploads/2020/05/netflix-poster-1024x576.jpg");
  background-size: cover;
  position: relative;

  .top {
    .wrapper {
      padding: 20px 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: rgb(5, 5, 5, 0.5);

      .logo {
        height: 40px;
      }

      .loginButton {
        background-color: red;
        border: none;
        color: white;
        border-radius: 5px;
        padding: 5px 15px;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
      }
      .loginButton:hover{
        box-shadow: 0 0 3px white;
      }
    }
  }

  .container {
    width: 100%;
    height: 80%;
    position: absolute;
    top: 1;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;

    h1 {
      font-size: 50px;
      color: gainsboro;
    }

    h2 {
      margin: 20px;
      color: gainsboro;
    }

    p {
      font-size: 20px;
    }

    .input {
      width: 50%;
      background-color: white;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 20px;
      height: 50px;
      border-radius: 5px;
      overflow: hidden;
      background-color: aliceblue;

      input{
          flex: 9;
          height: 100%;
          border: none;
          padding: 0 10px;
          font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
      }

      .registerButton{
          flex: 3;
          height: 100%;
          background-color: red;
          margin-left: auto;
          border: none;
          color: white;
          font-size: 22px;
          cursor: pointer;
          font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
      }
      .registerButton:hover{
        color: rgb(219, 219, 122);
      }
    }
  }
}
